.jumbotron {
    margin-top: 0;
    margin-bottom: 50px;

    background-image: url(/Clicky-game/static/media/grey_wash_wall.88ab8ad9.png);
    color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 50px;

    text-align: center;

    padding-top: 30px;
    padding-bottom: 30px;
}

.lead,
.alertText,
.score {
    font-size: 20px;
}
.bodyWrapper {
    width: 70%;
    background-color: white;
    display: flex;
    flex-flow: row wrap;
    padding: 50px;
    padding-bottom: 50px;
    margin-bottom: 100px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
    margin: auto;
}
.footer {
    text-align: center;
    font-size: 12px;
    height: 20px;
    background-color: rgb(58, 58, 58);
    color: white;
}
